html {
  background: #f1f1f1;
}

body {
  padding: 2rem 0;
  font-family: Azeret Mono, monospace;
}

@keyframes tailSwish {
  0% {
    rotate: none;
  }

  25% {
    rotate: -2deg;
  }

  27% {
    rotate: -28deg;
  }

  30% {
    rotate: -25deg;
  }

  36% {
    rotate: -25deg;
  }

  50% {
    rotate: -5deg;
  }

  75% {
    rotate: 10deg;
  }

  78% {
    rotate: 5deg;
  }

  98% {
    rotate: 5deg;
  }

  100% {
    rotate: none;
  }
}

@keyframes headLook {
  0% {
    rotate: none;
  }

  60% {
    rotate: none;
  }

  80% {
    rotate: -12deg;
  }

  100% {
    rotate: none;
  }
}

#whiskerStage {
  min-height: 75vh;
  background: #dadada;
  border-radius: 1rem;
  margin: 3rem 0;
  padding: 1.5rem;
  position: relative;
}

#whiskerStage h1, #whiskerStage nav, #whiskerStage aside {
  position: absolute;
}

#whiskerStage h1 {
  text-shadow: -.5vw .5vw #fff;
  margin: 1vw;
  font-size: 6.333vw;
  font-weight: bold;
  line-height: 1;
  left: 2vw;
}

#whiskerStage nav {
  position: absolute;
  bottom: 1.2vw;
  left: 0;
  right: 25vw;
}

#whiskerStage nav .button {
  width: 21%;
  min-height: 5vw;
  border-radius: 1rem;
  margin: 1%;
  padding: 1rem 0;
  transition: all .3s ease-in-out;
  box-shadow: 0 .25rem .125rem #64646440;
}

#whiskerStage nav .button:hover {
  outline: 2px solid green;
}

#whiskerStage nav .button:first-of-type {
  margin-left: 3%;
}

#whiskerStage nav .button:last-of-type {
  margin-right: 3%;
}

#whiskerStage nav .button em {
  margin-top: .5vw;
  padding-right: .67rem;
  display: inline-block;
  position: relative;
}

#whiskerStage nav .button em img {
  max-width: 3vw;
  max-height: 3vw;
}

#whiskerStage aside {
  width: 25vw;
  border-left: 1px solid #fff;
  padding: 2rem;
  top: 0;
  bottom: 0;
  right: 0;
}

#whiskerStage .slats-the-cat {
  width: 33vw;
  min-height: 40vw;
  background: url("slats-torso.001badc2.svg") 0 0 / contain no-repeat;
  position: absolute;
  top: 25vw;
  left: 25vw;
}

#whiskerStage .slats-the-cat .slats-head {
  position: absolute;
  top: -10vw;
  left: -2vw;
}

#whiskerStage .slats-the-cat .slats-tail {
  width: 38vw;
  min-height: 15vw;
  transform-origin: 35vw 0;
  background: url("slats-tail.5082d04e.svg") 0 0 / contain no-repeat;
  animation: tailSwish 12s linear infinite;
  display: inline-block;
  position: relative;
  bottom: -26vw;
  right: 5vw;
}

.slats-head {
  width: 18vw;
  min-height: 20vw;
  background: url("slats-head.d381f18c.svg") 0 0 / contain no-repeat;
  animation: headLook 12s linear infinite;
  display: inline-block;
  position: relative;
}

.slats-head img {
  width: 100%;
  display: none;
  position: absolute;
  top: 9vw;
  left: -2vw;
}

.slats-head.slats-resting img.is-resting, .slats-head.slats-blink img.is-blink, .slats-head.slats-eating2 img.is-eating2, .slats-head.slats-eating img.is-eating, .slats-head.slats-huh img.is-huh, .slats-head.slats-grumpy img.is-grumpy {
  display: inline-block;
}

#elements {
  position: relative;
}

#elements svg, #elements img {
  max-width: 20vw;
  height: auto;
}

