html {
    background: #F1F1F1;
}
body {
    font-family: 'Azeret Mono', monospace;
    padding: 2rem 0;
}

@keyframes tailSwish {
    0%   { rotate: 0deg; }
    25%  { rotate: -2deg; }
    27%  { rotate: -28deg; }
    30%  { rotate: -25deg; }
    36%  { rotate: -25deg; }
    50%  { rotate: -5deg; }
    75%  { rotate: 10deg; }
    78%  { rotate: 5deg; }
    98%  { rotate: 5deg; }
    100% { rotate: 0deg; }
}

@keyframes headLook {
    0%   { rotate: 0deg; }
    60%  { rotate: 0deg; }
    80%  { rotate: -12deg; }
    100% { rotate: 0deg; }
}

#whiskerStage {
    border-radius: 1rem;
    background: #DADADA;
    padding: 1.5rem;
    position: relative;
    min-height: 75vh;
    margin: 3rem 0 3rem;

    h1,
    nav,
    aside {
        position: absolute;
    }

    h1 {
        font-size: 6.333vw;
        font-weight: bold;
        line-height: 1;
        margin: 1vw;
        left: 2vw;
        text-shadow: -0.5vw 0.5vw #fff;
    }

    nav {
        position: absolute;
        bottom: 1.2vw;
        left: 0;
        right: 25vw;

        .button {
            width: 21%;
            margin: 1%;
            padding: 1rem 0;
            min-height: 5vw;
            border-radius: 1rem;
            box-shadow: 0 0.25rem 0.125rem 0 rgba(100,100,100,0.25);
            transition: all 0.3s ease-in-out;

            &:hover {
                outline: 2px solid green;
            }

            &:first-of-type {
                margin-left: 3%;
            }
            &:last-of-type {
                margin-right: 3%;
            }

            em {
                position: relative;
                display: inline-block;
                padding-right: 0.67rem;
                margin-top: 0.5vw;

                img {
                    max-width: 3vw;
                    max-height: 3vw;
                }
            }
        }
    }

    aside {
        width: 25vw;
        right: 0;
        top: 0;
        bottom: 0;
        border-left: 1px solid white;
        padding: 2rem;
    }

    .slats-the-cat {
        position: absolute;
        top: 25vw;
        left: 25vw;
        width: 33vw;
        min-height: 40vw;
        background: url('../images/slats-torso.svg') no-repeat 0 0;
        background-size: contain;

        .slats-head {
            position: absolute;
            top: -10vw;
            left: -2vw;
        }

        .slats-tail {
            position: absolute;
            bottom: -26vw;
            right: 5vw;
            background: url('../images/slats-tail.svg') no-repeat 0 0;
            background-size: contain;
            width: 38vw;
            min-height: 15vw;
            display: inline-block;
            position: relative;
            transform-origin: 35vw 0%;
            animation: tailSwish 12s linear infinite;
        }
    }
}

.slats-the-cat {

}

.slats-head {
    background: url('../images/slats-head.svg') no-repeat 0 0;
    background-size: contain;
    width: 18vw;
    min-height: 20vw;
    display: inline-block;
    position: relative;
    animation: headLook 12s linear infinite;

    img {
        display: none;
        position: absolute;
        top: 9vw;
        left:-2vw;
        width: 100%;
    }

    &.slats-resting {
        img.is-resting {
            display: inline-block;
        }
    }

    &.slats-blink {
        img.is-blink {
            display: inline-block;
        }
    }

    &.slats-eating2 {
        img.is-eating2 {
            display: inline-block;
        }
    }

    &.slats-eating {
        img.is-eating {
            display: inline-block;
        }
    }

    &.slats-huh {
        img.is-huh {
            display: inline-block;
        }
    }

    &.slats-grumpy {
        img.is-grumpy {
            display: inline-block;
        }
    }
}

#elements {
    position: relative;
    svg, img {
        max-width: 20vw;
        height: auto;
    }
}